import Component from 'flarum/common/Component';
import app from 'flarum/forum/app';
import IndexSidebar from 'flarum/forum/components/IndexSidebar';
import SelectDropdown from 'flarum/common/components/SelectDropdown';
export default class ForumNav extends Component {
  view() {
    return m("div", {
      className: "BlogForumNav BlogSideWidget"
    }, m("h3", null, app.translator.trans('vadkuz-flarum2-blog.forum.forum_nav')), m("nav", {
      className: "IndexPage-nav sideNav"
    }, m(SelectDropdown, {
      buttonClassName: "Button",
      className: "App-titleControl"
    }, this.navItems().toArray())));
  }
  navItems() {
    return IndexSidebar.prototype.navItems();
  }
}
flarum.reg.add('vadkuz-flarum2-blog', 'forum/components/ForumNav', ForumNav);