import Modal from 'flarum/common/components/Modal';
import Button from 'flarum/common/components/Button';
import ItemList from 'flarum/common/utils/ItemList';
import Stream from 'flarum/common/utils/Stream';
export default class RenameArticleModal extends Modal {
  oninit(vnode) {
    super.oninit(vnode);
    this.article = this.attrs.article;
    this.name = Stream(this.article.title() || '');
    this.redirect = this.attrs.redirect;
  }
  className() {
    return 'Modal--small Support-Modal';
  }
  title() {
    return app.translator.trans('vadkuz-flarum2-blog.forum.tools.rename_article');
  }
  content() {
    return m("div", {
      className: "Modal-body"
    }, m("div", {
      className: "Form"
    }, this.fields().toArray()));
  }
  fields() {
    const items = new ItemList();
    items.add('name', m("div", {
      className: "Form-group"
    }, m("label", null, app.translator.trans('vadkuz-flarum2-blog.forum.article.title'), ":"), m("input", {
      className: "FormControl",
      placeholder: app.translator.trans('vadkuz-flarum2-blog.forum.article.title'),
      bidi: this.name
    })), 50);
    items.add('submit', m("div", {
      className: "Form-group"
    }, Button.component({
      type: 'submit',
      className: 'Button Button--primary SupportModal-save',
      loading: this.loading
    }, app.translator.trans('core.forum.composer_edit.submit_button'))), -10);
    return items;
  }
  submitData() {
    return {
      title: this.name()
    };
  }
  onsubmit(e) {
    e.preventDefault();
    this.loading = true;

    // Do not save
    if (this.attrs.onChange) {
      this.attrs.onChange(this.name());
      this.hide();
      return;
    }
    this.article.save({
      title: this.name()
    }).then(() => {
      this.hide();

      // Redirect
      if (this.redirect) {
        const url = "/blog/".concat(this.article.slug());
        m.route.set(url, true);
        window.history.replaceState(null, document.title, url);
      }
    }, response => {
      this.loading = false;
      this.handleErrors(response);
    });
  }
}
flarum.reg.add('vadkuz-flarum2-blog', 'forum/components/Modals/RenameArticleModal', RenameArticleModal);